import axios from 'axios';
import {
  FormInputBox,
  HiddenInput,
  InputNote,
  Label,
} from 'components/LeadForm/LeadForm.styled';
import { Loader } from 'components/SharedLayout/Loaders/Loader';
import { Formik, useFormikContext } from 'formik';
import { useState } from 'react';
import * as yup from 'yup';
import {
  FormBottomStar,
  PageForm,
} from '../../LeadFormPage/LeadFormPage.styled';
import {
  BackgroundFilterBottom,
  BackgroundFilterTop,
  BackgroungStarLarge,
  BackgroungStarSmall,
  CurrentPage,
  Description,
  Logo,
  NextPageBtn,
  PageCounter,
  Pagination,
  PreviousPageBtn,
  QuizArrowLeft,
  QuizArrowRight,
  QuizBox,
  QuizFormLink,
  QuizInput,
  Title,
} from '../Quiz.styled';

axios.defaults.baseURL = 'https://ap-server-8qi1.onrender.com';

export const QuizQuestionFormOneRequest = ({
  nextQuestion,
  quizValues,
  activeSlide,
  previousQuestion,
  lang,
}) => {
  const [isLoading, setIsLoading] = useState(false);

  const initialValues = {
    name: '',
    phone: '',
    mail: quizValues.current.mail,
    password: quizValues.current.password,
    tag: quizValues.current.tag,
    lang: quizValues.current.lang,
    adult: quizValues.current.adult,
    age: quizValues.current.age,
    knowledge: quizValues.current.knowledge,
    quantity: quizValues.current.quantity,
    difficulties: quizValues.current.difficulties,
    interests: quizValues.current.interests,
  };

  const leadSchema = yup.object().shape({
    name: yup
      .string()
      .required("–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–∫–∞–∂—ñ—Ç—å —Å–≤–æ—î —ñ–º'—è!")
      .matches(
        /^[A-Za-z–ê-–Ø–∞-—è—ñ–Ü—ó–á—î–Ñ]/,
        "–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å —ñ–º'—è, –±–µ–∑ —Ü–∏—Ñ—Ä —Ç–∞ —Å–ø–µ—Ü—Å–∏–º–≤–æ–ª—ñ–≤!"
      )
      .min(2, '–ù–µ–æ–±—Ö—ñ–¥–Ω–æ –≤–≤–µ—Å—Ç–∏ –Ω–µ –º–µ–Ω—à–µ –Ω—ñ–∂ 2 —Å–∏–º–≤–æ–ª–∏!')
      .max(50, '–ù–µ–æ–±—Ö—ñ–¥–Ω–æ –≤–≤–µ—Å—Ç–∏ –Ω–µ –±—ñ–ª—å—à–µ –Ω—ñ–∂ 50 —Å–∏–º–≤–æ–ª—ñ–≤!'),
    phone: yup
      .string()
      .required('–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–∫–∞–∂—ñ—Ç—å —Å–≤—ñ–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É!')
      .matches(
        /\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}/,
        '–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å –≤–∞–ª—ñ–¥–Ω–∏–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É!'
      )
      .min(10, '–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É –º–∞—î —Å–∫–ª–∞–¥–∞—Ç–∏—Å—è –Ω–µ –º–µ–Ω—à–µ –Ω—ñ–∂ –∑ 10 —Å–∏–º–≤–æ–ª—ñ–≤!')
      .max(15, '–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É –º–∞—î —Å–∫–ª–∞–¥–∞—Ç–∏—Å—è –Ω–µ –±—ñ–ª—å—à–µ –Ω—ñ–∂ –∑ 15 —Å–∏–º–≤–æ–ª—ñ–≤!'),
    mail: yup.string().required(),
    password: yup.string().required(),
    tag: yup.string().required(),
    lang: yup.string().required(),
    adult: yup.boolean().required(),
    age: yup.string().required(),
    knowledge: yup.string().required(),
    quantity: yup.string().required(),
    difficulties: yup.string().required(),
    interests: yup.string().required(),
  });

  const QuizSubmitLink = () => {
    console.log(useFormikContext());

    const { values, isValid, submitForm } = useFormikContext();
    return (
      <QuizFormLink
        data-gtm="quizformsubmit"
        href={quizValues.current.leadPage}
        onClick={async e => {
          e.preventDefault();
          console.log(isValid);
          if (values.name && values.phone && isValid) {
            console.log('valid');
            await submitForm();
          }
        }}
      >
        –ü–µ—Ä–µ–π—Ç–∏ –≤ –º–µ—Å–µ–Ω–¥–∂–µ—Ä
      </QuizFormLink>
    );
  };

  const handleSubmit = async (values, { resetForm }) => {
    setIsLoading(isLoading => (isLoading = true));

    const userSubmit = async (crmId, contactId) => {
      const userValues = {
        name: values.name.trim().trimStart(),
        mail: values.mail,
        password: values.password,
        pupilId: '0000000',
        crmId: crmId,
        contactId: contactId,
        age: quizValues.current.age,
        lang:
          quizValues.current.lang === 'en' && !quizValues.current.adult
            ? 'enkids'
            : quizValues.current.lang,
        course: '0',
        package: '–ú–∞—Ä–∞—Ñ–æ–Ω',
        knowledge: quizValues.current.knowledge,
        manager: '-',
      };

      try {
        const response = await axios.post('/users/new', userValues);
        console.log(response);
      } catch (error) {
        console.error(error);
      }
    };

    try {
      const response = await axios.post('/leads/quiz-one/', values);
      console.log(response);
      quizValues.current.leadPage = response.data.engPage;
      quizValues.current.crmId = response.data.crmId;
      quizValues.current.contactId = response.data.contactId;
      console.log(response.data.crmId, response.data.contactId);
      userSubmit(response.data.crmId, response.data.contactId);
      // alert('reseting')
      setTimeout(() => {
        resetForm();
        setIsLoading(isLoading => (isLoading = false));
        window.location.replace(quizValues.current.leadPage);
      }, 1500);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <>
      <QuizBox>
        <Logo />
        <Title>–ú–∞–π–∂–µ –≥–æ—Ç–æ–≤–æ!</Title>
        <Description>
          –©–æ–± –æ—Ç—Ä–∏–º–∞—Ç–∏ –¥–æ—Å—Ç—É–ø –¥–æ {lang ? '–ø—Ä–æ–±–Ω–∏—Ö –∑–∞–Ω—è—Ç—å' : '–º–∞—Ä–∞—Ñ–æ–Ω—É'}, –≤–≤–µ–¥—ñ—Ç—å
          —Å–≤–æ—î —ñ–º‚Äô—è —ñ –∞–∫—Ç—É–∞–ª—å–Ω–∏–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É —Ç–∞ –ø–µ—Ä–µ–π–¥—ñ—Ç—å —É –∑—Ä—É—á–Ω–∏–π –¥–ª—è –≤–∞—Å
          –º–µ—Å–µ–Ω–¥–∂–µ—Ä.
        </Description>
        <Formik
          initialValues={initialValues}
          onSubmit={handleSubmit}
          validationSchema={leadSchema}
        >
          <PageForm>
            <FormBottomStar />
            <FormInputBox>
              <Label>
                <QuizInput type="text" name="name" placeholder="–Ü–º'—è*" />
                <InputNote component="p" name="name" />
              </Label>
              <Label>
                <QuizInput type="tel" name="phone" placeholder="–¢–µ–ª–µ—Ñ–æ–Ω*" />
                <InputNote component="p" name="phone" />
              </Label>
            </FormInputBox>
            <HiddenInput type="text" name="tag" />
            <HiddenInput type="text" name="lang" />
            <HiddenInput type="text" name="adult" />
            <HiddenInput type="text" name="age" />
            <HiddenInput type="text" name="knowledge" />
            <HiddenInput type="text" name="quantity" />
            <HiddenInput type="text" name="difficulties" />
            <HiddenInput type="text" name="interests" />
            <QuizSubmitLink />
            {isLoading && <Loader />}
          </PageForm>
        </Formik>
        <BackgroundFilterTop /> <BackgroundFilterBottom />
        <BackgroungStarSmall /> <BackgroungStarLarge />
        <Pagination>
          <PreviousPageBtn
            className={activeSlide - 1 < 1 && 'disabled'}
            disabled={activeSlide - 1 < 1 && true}
            onClick={previousQuestion}
          >
            <QuizArrowLeft />
          </PreviousPageBtn>
          <PageCounter>
            <CurrentPage>{activeSlide}</CurrentPage>/{lang ? 7 : 8}
          </PageCounter>
          <NextPageBtn className="hidden" onClick={nextQuestion}>
            <QuizArrowRight />
          </NextPageBtn>
        </Pagination>
      </QuizBox>
    </>
  );
};
